import { ReactNode } from 'react';
import NextLink from 'next/link';

const Link = ({ children, href, button = false }: { children: ReactNode; href: string; button?: boolean }) => (
  <NextLink href={href}>
    {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
    <a>{children}</a>
  </NextLink>
);

export default Link
